<template>
  <div class="container">
    <!--下啦刷新-->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <!--上啦加载-->
      <van-list
        v-if="orderList.length > 0"
        v-model="loading"
        :finished="finished"
        :finishedText="''"
        @load="onLoad"
        van-clearfix
        :immediate-check="false"
      >
        <div class="item-container" v-for="item in orderList" :key="item.id">
          <div class="title-row">
            <span class="title">{{item.storeName}}</span>
            <span class="status-text">{{getOrderStatus(item)}}</span>
          </div>
          <div class="order-id">
           {{(item.orderType === 'N' ? ' 零售单：' : '零售退单：') + item.orderNo}}
          </div>
          <div class="flex-row prod-item" v-for="product in item.ylhOrderProductsList" :key="product.id">
            <img class="prod-img" v-lazy="product.mainImgUrl" alt/>
            <div style="width: 4.7rem">
              <div class="prod-name">{{product.prodName}}</div>
              <div class="prod-num">x{{product.number}}</div>
            </div>
          </div>
          <div class="prod-sum">
            共{{getItemOrderSum(item)}}件商品  合计： <span class="prod-price">￥<span style="font-size: .36rem">{{item.payment.toFixed(2).split('.')[0]}}</span>.{{item.payment.toFixed(2).split('.')[1]}}</span>
          </div>
          <div class="order-date">
            下单时间：{{item.created | formatDate}}
          </div>
          <div class="track-btn-container" v-if="item.orderType === 'N'">
            <div class="track-btn" @click="toTrackDetail(item.orderNo)">
              查看物流
            </div>
          </div>
        </div>
        <div style="height: .3rem"></div>
      </van-list>
      <div v-if="finished && orderList.length === 0" class="no-data">
        <div class="nodata-icon"> </div>
        <div class="no-bottom">暂无订单</div>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import refreshLoading from '@/components/refreshLoading'
import utils from '@/utils/utils';
/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
const status = {
  'N': ['', '', '', '', '待发货', '部分发货', '待出库', '部分出库', '待收货', '部分收货', '已完成', '已取消'],
  'Y': ['', '待收货', '部分收货', '已取消', '待入库', '部分入库', '已完成']
};
export default {
  name: 'storeOrderList', // 易理货订单
  components: {
    refreshLoading
  },
  data() {
    return {
      isLoading: false,
      loading: true,
      finished: true,
      pageIndex: 0,
      orderList: [],
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    onRefresh() {
      const context = this;
      context.pageIndex = 0;
      // 加载状态结束
      context.loading = false;
      context.finished = true;
      context.isLoading = true;
      /**
        * @description 我的订单列表
      */
      context.getList();
    },

    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      this.pageIndex++;
      /**
       * @description 个人中心 我的订单列表
      */
      this.getList();
    },
    getList() {
      const context = this;
      const params = {
        pageIndex: context.pageIndex,
        pageSize: 10,
        orderFlag: 0, // 订单归属 0:全部订单，1：我的订单，2：用户订单
      };
      personalApi.getStoreOrderList(params).then((res) => {
        console.log('订单：', res);
        if (res.success) {
          if (context.pageIndex === 0) {
            context.orderList = res.data.orders;
          } else {
            context.orderList = [...context.orderList, ...res.data.orders];
          }
          // 加载状态结束
          context.finished = false;
          context.loading = false;
          if (context.isLoading) {
            context.isLoading = false;
          }
          if (res.data.orders.length < 10) {
            context.finished = true;
          }
        } else {
          this.$toast.fail(res.message);
          context.finished = true;
          context.loading = false;
        }
        if (context.isLoading) {
          context.isLoading = false;
        }
      }).catch(() => {
        context.finished = true;
        context.loading = false;
        context.isLoading = false;
      });
    },
    getOrderStatus(order) {
      return status[order.orderType][Number(order.orderStatus)]
    },
    getItemOrderSum(order) {
      let orderSum = 0;
      if (order.ylhOrderProductsList && order.ylhOrderProductsList.length) {
        order.ylhOrderProductsList.map(prod => {
          orderSum = orderSum + prod.number;
        });
      }
      return orderSum;
    },
    /**
     * @description 跳转物流中间页
     * @param  n : 零售单号
    */
    toTrackDetail(n) {
      //  中间页
      this.$router.push({
        path: `/trackingDetail?soNo=${n}`
      });
    }
  },
  filters: {
    formatDate(date) {
      return utils.formatTime(date, 'Y-M-D h:m:s')
    }
  },
}
</script>
